import { PassportStrategy } from '@nestjs/passport'
import { Strategy } from 'passport-local'
import { Injectable, Logger } from '@nestjs/common'
import { UserService } from '@flowda-projects/flowda-services'

@Injectable()
export class UserLocalStrategy extends PassportStrategy(Strategy, 'userLocal') {
  private readonly logger = new Logger(UserLocalStrategy.name)

  constructor(private user: UserService) {
    super({
      usernameField: 'username',
      passwordField: 'password',
    })
  }

  async validate(username: string, password: string) {
    const ret = await this.user.validate({ username, password })
    return ret.user
  }
}
