import { ContainerModule } from 'inversify'
import { bindService, ServiceSymbol } from '@flowda-projects/flowda-shared'
import { TrpcService } from './trpc/trpc.service'
import { TrpcRouter } from './trpc/trpc.router'
import { SchemaRouter } from './trpc/schema.router'
import { UserRouter } from './trpc/user.router'
import { ContextFactory } from './trpc/trpc.context'
import { DataRouter } from './trpc/data.router'
import { CustomerAuthV4Router } from './trpc/sdk-v4/customer-auth-v4.router'
import { OrderV4Router } from './trpc/sdk-v4/order-v4.router'
import { ProductV4Router } from './trpc/sdk-v4/product-v4.router'

export const flowdaServiceTrpcServerModule = new ContainerModule(bind => {
  bind(TrpcService).toSelf().inSingletonScope()
  bind(ContextFactory).toSelf().inSingletonScope()
  bind(SchemaRouter).toSelf().inSingletonScope()
  bind(UserRouter).toSelf().inSingletonScope()
  bind(DataRouter).toSelf().inSingletonScope()

  bind(CustomerAuthV4Router).toSelf().inSingletonScope()
  bind(OrderV4Router).toSelf().inSingletonScope()
  bind(ProductV4Router).toSelf().inSingletonScope()

  bindService(bind, ServiceSymbol, TrpcRouter)
})
