import { transactionsNativeSchemaDto, TWxPayQuery, WechatpayNodeV3Symbol } from '@flowda-projects/flowda-shared-types'
import WechatpayNodeV3 from 'wechatpay-node-v3'
import { InternalServerErrorException, LoggerService } from '@nestjs/common'
import { inject, injectable } from 'inversify'
import { getTimeExpire } from '@flowda-projects/flowda-shared'

@injectable()
export class WeixinPayService {
  protected readonly logger: LoggerService

  constructor(
    @inject(WechatpayNodeV3Symbol) private wechatPayNodeV3: WechatpayNodeV3,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(WeixinPayService.name)
    this.transactionsNative = this.transactionsNative.bind(this)
    this.transactionsJSAPI = this.transactionsJSAPI.bind(this)
  }

  /*
  {
  "status": 200,
  "appId": "xx",
  "timeStamp": "1682132086",
  "nonceStr": "xx",
  "package": "prepay_id=xx",
  "signType": "RSA",
  "paySign": "xx"
}
   */
  async transactionsJSAPI(input: transactionsNativeSchemaDto): Promise<{
    prepay_id: string
  }> {
    const timeExpire = getTimeExpire(5)
    const params = {
      description: input.desc, ///
      out_trade_no: String(input.orderId), ///_-*
      time_expire:
        timeExpire /*rfc3339yyyy-MM-DDTHH:mm:ss+TIMEZONEyyyy-MM-DDTtimeHH:mm:ssTIMEZONE+08:00UTC82015-05-20T13:29:35+08:002015520 132935 */,
      attach: '', ///API
      notify_url: 'https://www.weixin.qq.com/wxpay/pay.php', // todo /* urlurl httpsNAT IPhttp */
      support_fapiao: false, ///true
      amount: {
        total: input.total * 100, ///
        currency: 'CNY', /// CNY
      },
      payer: {
        openid: input.openid!, ///appid
      },
      settle_info: {
        profit_sharing: false, ///
      },
    }
    const wxRet = await this.wechatPayNodeV3.transactions_jsapi(params)
    this.logger.log(`wechat transactions_jsapi resp ${JSON.stringify(wxRet)}`)
    if (wxRet['status'] !== 200) {
      throw new InternalServerErrorException(' (jsapi) ')
    }
    return wxRet as { prepay_id: string }
  }

  /*
  {"status":200,"code_url":"weixin://wxpay/bizpayurl?pr=pUnqLjbzz"}
   */
  async transactionsNative(input: transactionsNativeSchemaDto): Promise<{
    status: number
    code_url: string
  }> {
    const timeExpire = getTimeExpire(5)
    const params = {
      description: input.desc, ///
      out_trade_no: String(input.orderId), ///_-*
      // '2022-11-11T23:59:59+08:00'
      time_expire: timeExpire,
      /*rfc3339yyyy-MM-DDTHH:mm:ss+TIMEZONEyyyy-MM-DDTtimeHH:mm:ssTIMEZONE+08:00UTC82015-05-20T13:29:35+08:002015520 132935 */
      attach: '', ///API
      notify_url: 'https://www.weixin.qq.com/wxpay/pay.php', // todo /* urlurl httpsNAT IPhttp */
      support_fapiao: false, ///true
      amount: {
        total: input.total * 100, ///
        currency: 'CNY', /// CNY
      },
      settle_info: {
        profit_sharing: false, ///
      },
    }
    this.logger.log(`wechat start to transactions_native ${JSON.stringify(params)}`)
    const wxRet = (await this.wechatPayNodeV3.transactions_native(params)) as { status: number; code_url: string }
    this.logger.log(`wechat transactions_native resp ${JSON.stringify(wxRet)}`)
    if (wxRet.status !== 200) {
      throw new InternalServerErrorException(' (native) ')
    }
    return wxRet
  }

  /*
  {
    "status": 200,
    "amount": {
        "currency": "CNY",
        "payer_currency": "CNY",
        "payer_total": 1,
        "total": 1
    },
    "appid": "wx6ecc94d7d5133fde",
    "attach": "",
    "bank_type": "OTHERS",
    "mchid": "1634638724",
    "out_trade_no": "claz2v5la0000tzp2ivnp5gpm",
    "payer": {
        "openid": "oQBzz5GM-9aCngjG3eNpqJIlzJj4"
    },
    "promotion_detail": [],
    "success_time": "2022-11-27T16:07:27+08:00",
    "trade_state": "SUCCESS",
    "trade_state_desc": "",
    "trade_type": "NATIVE",
    "transaction_id": "4200001645202211278892941061"
}
   */
  async query(outTradeNo: number) {
    this.logger.log(`wx pay query with tradeNo ${outTradeNo}`)
    const ret = await this.wechatPayNodeV3.query({ out_trade_no: String(outTradeNo) })
    this.logger.log(`wx pay query resp: ${JSON.stringify(ret)}`)
    return ret as TWxPayQuery
  }
}
