import {
  DynamicTableDataWithRelationsSchema,
  DynamicTableDefColumnWithRelationsSchema,
  DynamicTableDefWithRelationsSchema,
  MenuWithRelationsSchema,
  TaskFormRelationSchema,
  TenantWithRelationsSchema,
  UserSchema,
} from '@flowda-projects/prisma-flowda'
import { meta } from '@flowda-projects/flowda-shared'
import { z } from 'zod'

export const UserResourceSchema = UserSchema.omit({
  hashedPassword: true,
  hashedRefreshToken: true,
})
  .extend({
    // todo:  optional,    password 
    //  extend password  createRequestUrl
    password: z.string().optional().openapi({
      title: '',
      prisma: false,
    }),
    __meta: meta({
      extends: 'UserSchema',
    }),
  })
  .openapi({
    custom: {
      route_prefix: '/tenant_admin/tenant',
    },
  })

export const TenantResourceSchema = TenantWithRelationsSchema.extend({
  __meta: meta({
    extends: 'TenantSchema',
  }),
}).openapi({
  custom: {
    route_prefix: '/tenant_admin/tenant',
  },
})

export const MenuResourceSchema = MenuWithRelationsSchema.extend({
  __meta: meta({
    extends: 'MenuSchema',
  }),
}).openapi({
  custom: {
    route_prefix: '/tenant_admin/tenant',
  },
})

export const TaskFormRelationResourceSchema = TaskFormRelationSchema.extend({
  __meta: meta({
    extends: 'TaskFormRelationSchema',
  }),
}).openapi({
  custom: {
    route_prefix: '/workflows/manage',
  },
})

// prisma:false camunda API
export const TaskResourceSchema = z
  .object({
    id: z.string().cuid().openapi({ title: ' Id' }),
    name: z.string().openapi({ title: '' }),
    assignee: z.string().openapi({ title: '' }),
    created: z.date().openapi({ title: '' }),
    processDefinitionId: z.string().openapi({ title: '' }),
    processInstanceId: z.string().openapi({ title: '' }),
    taskDefinitionKey: z.string().openapi({ title: '' }),
    variables: z.string().openapi({ title: '' }),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'name',
    display_primary_key: false,
  })

export const ProcessDefinitionResourceSchema = z
  .object({
    id: z.string(),
    key: z.string(),
    name: z.string(),
    version: z.number(),
    resource: z.string(),
    deploymentId: z.string().cuid(),
    suspended: z.boolean(),
    historyTimeToLive: z.number(),
    startableInTasklist: z.boolean(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'name',
    display_primary_key: 'true',
  })

export const WorkflowUserResourceSchema = z
  .object({
    id: z.string(),
    firstName: z.string(),
    lastName: z.string(),
    email: z.string(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '',
    display_column: 'id',
  })

