import { Body, Controller, Delete, Get, HttpCode, Post, Put, Req } from '@nestjs/common'
import { DynamicTableDataService } from '@flowda-projects/flowda-services'
import * as express from 'express'

@Controller('/dynamic-table-data/*')
export class DynamicTableDataController {
  constructor(private readonly service: DynamicTableDataService) {}

  @Get('')
  async get(@Req() req: express.Request) {
    return this.service.get(req.user, req.params[0], req.query)
  }

  @Post('')
  post(@Req() req: express.Request, @Body() values: any) {
    return this.service.post(req.user, req.params[0], values)
  }

  @Put('')
  async put(@Req() req: express.Request, @Body() values: any) {
    return this.service.put(req.user, req.params[0], values)
  }

  @Delete('')
  @HttpCode(200)
  remove(@Req() req: express.Request) {
    return this.service.remove(req.user, req.params[0])
  }
}
