import { Controller, Get } from '@nestjs/common'
import { DynamicTableDefService } from '@flowda-projects/flowda-services'

@Controller('/dynamic-table-def')
export class DynamicTableDefController {
  constructor(private readonly defService: DynamicTableDefService) {}

  @Get('/getSchema')
  getSchema() {
    return this.defService.getSchema()
  }

  @Get('/getRaw')
  getRaw() {
    return this.defService.getRaw()
  }
}
