import { Module } from '@nestjs/common'
import { APP_FILTER, APP_PIPE } from '@nestjs/core'

import { AppController } from './app.controller'
import { ServicesModule } from '../services/services.module'
import { AppExceptionFilter } from '@flowda-projects/flowda-shared-node'
import { TaskController } from './task.controller'
import { UserController } from '../user/user.controller'
import { UserLocalStrategy } from '../user/userLocal.strategy'
import { UserJwtStrategy } from '../user/userJwt.strategy'
import { DataController } from './data.controller'
import { ScheduleModule } from '@nestjs/schedule'
import { TableFilterController } from './table-filter.controller'
import { AuditController } from './audit.controller'
import { ZodValidationPipe } from 'nestjs-zod'
import { DynamicTableDefController } from './dynamic-table-def.controller'
import { DynamicTableDataController } from './dynamic-table-data.controller'
import { MenuController } from './menu.controller'

export const appModuleControllers = [
  AppController,
  DataController,
  TaskController,
  UserController,
  TableFilterController,
  AuditController,
  DynamicTableDefController,
  DynamicTableDataController,
  MenuController,
]
export const appModuleProviders = [
  {
    provide: APP_FILTER,
    useClass: AppExceptionFilter,
  },
  {
    provide: APP_PIPE,
    useClass: ZodValidationPipe,
  },
  UserLocalStrategy,
  UserJwtStrategy,
]

@Module({
  imports: [ServicesModule, ScheduleModule.forRoot()],
  controllers: appModuleControllers,
  providers: appModuleProviders,
})
export class AppModule {}
