import { ContainerModule, interfaces } from 'inversify'
import {
  APISymbol,
  bindService,
  bindServiceSymbol,
  CustomZodSchemaSymbol,
  PrismaZodSchemaSymbol,
  ServiceSymbol,
  WechatOAuthSymbol,
} from '@flowda-projects/flowda-shared'
import { zt } from '@flowda-projects/prisma-flowda'
import * as schema from './lib/schema'
import { TaskService } from './services/task.service'
import { UserService } from './services/user.service'
import type { AxiosInstance } from 'axios'
import { axiosApiInstance } from './axios'
import { DynamicTableDefService } from './services/dynamic-table-def.service'
import { DynamicTableDataService } from './services/dynamic-table-data.service'
import {
  DynamicTableDataServiceSymbol,
  DynamicTableDefServiceSymbol,
  MenuServiceSymbol,
  WechatpayNodeV3Symbol,
} from '@flowda-projects/flowda-shared-types'
import { MenuService } from './services/menu.service'
import WechatOAuth from 'wechat-oauth'
import { FLOWDA_ENV } from './lib/flowda-env'
import { WeixinLoginService } from './services/weixin-login.service'
import { WeixinFuwuhaoLoginService } from './services/weixin-fuwuhao-login.service'
import WechatpayNodeV3 from 'wechatpay-node-v3'
import { OrderV4Service } from './services/sdk/orderV4.service'
import { ProductV4Service } from './services/sdk/productV4.service'
import { WeixinPayService } from './services/weixin-pay.service'

export const flowdaServiceModule = new ContainerModule((bind: interfaces.Bind) => {
  bind(PrismaZodSchemaSymbol).toConstantValue(zt)
  bind(CustomZodSchemaSymbol).toConstantValue(schema)
  bind<AxiosInstance>(APISymbol).toConstantValue(axiosApiInstance)

  bind<WechatOAuth>(WechatOAuthSymbol).toConstantValue(
    new WechatOAuth(FLOWDA_ENV.freecharger_pc_appid, FLOWDA_ENV.freecharger_pc_appSecret),
  )
  bind<WechatpayNodeV3>(WechatpayNodeV3Symbol).toConstantValue(
    new WechatpayNodeV3({
      appid: FLOWDA_ENV.appid,
      mchid: FLOWDA_ENV.mchid,
      publicKey: Buffer.from(FLOWDA_ENV['apiclient_cert.pem'], 'utf-8'),
      privateKey: Buffer.from(FLOWDA_ENV['apiclient_key.pem'], 'utf-8'),
    }),
  )

  bindService(bind, ServiceSymbol, TaskService)
  bindService(bind, ServiceSymbol, UserService)
  bindServiceSymbol(bind, ServiceSymbol, DynamicTableDefServiceSymbol, DynamicTableDefService)
  bindServiceSymbol(bind, ServiceSymbol, DynamicTableDataServiceSymbol, DynamicTableDataService)
  bindServiceSymbol(bind, ServiceSymbol, MenuServiceSymbol, MenuService)

  bind(WeixinLoginService).toSelf().inSingletonScope()
  bind(WeixinFuwuhaoLoginService).toSelf().inSingletonScope()
  bind(WeixinPayService).toSelf().inSingletonScope()
  bind(OrderV4Service).toSelf().inSingletonScope()
  bind(ProductV4Service).toSelf().inSingletonScope()
})
