import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-flowda'
import { matchPath, PrismaClientSymbol } from '@flowda-projects/flowda-shared'

@injectable()
export class DynamicTableDataService {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DynamicTableDataService.name)
  }

  async get(reqUser: any, path: string, query: any) {
    this.logger.debug!(
      `get: ${path}, query: ${JSON.stringify(query, null, 2)}, reqUser: ${JSON.stringify(reqUser, null, 2)}`,
    )
    const parsedPath = matchPath(path)
    if (parsedPath.length === 0) return Promise.resolve({})
    const { resource, id, resourceSchema } = parsedPath[parsedPath.length - 1]
    if (id == null) {
      const where: db.Prisma.DynamicTableDataWhereInput = {
        isDeleted: false,
        tenantId: reqUser.tid,
        dynamicTableDef: {
          name: resource,
          tenantId: reqUser.tid,
          isDeleted: false,
        },
      }
      const dbRet = await this.prisma.$transaction([
        this.prisma.dynamicTableData.findMany({
          where,
        }),
        this.prisma.dynamicTableData.count({
          where: where,
        }),
      ])
      const [data, count] = dbRet
      const ret = data.map(item => {
        return Object.assign(
          {
            id: item.id,
          },
          item.data,
        )
      })
      return {
        pagination: {
          total: count,
        },
        data: ret,
      }
    } else {
      const dbRet = await this.prisma.dynamicTableData.findUniqueOrThrow({
        where: {
          id: id as number,
        },
      })
      return Object.assign(
        {
          id: dbRet.id,
        },
        dbRet.data,
      )
    }
  }

  async put(reqUser: any, path: string, values: any) {
    this.logger.debug!(
      `put: ${path}, values: ${JSON.stringify(values, null, 2)}, reqUser: ${JSON.stringify(reqUser, null, 2)}`,
    )
    const parsedPath = matchPath(path)
    if (parsedPath.length === 0) return Promise.resolve({})
    const { id } = parsedPath[parsedPath.length - 1]

    const prevRet = await this.prisma.dynamicTableData.findUniqueOrThrow({
      where: {
        id: id as number,
      },
      select: {
        id: true,
        tenantId: true,
        data: true,
      },
    })
    const data2 = Object.assign(prevRet.data as any, values)
    const ret = await this.prisma.dynamicTableData.update({
      where: {
        id: id as number,
      },
      data: {
        data: data2,
      },
    })
    return Object.assign(
      {
        id: ret.id,
      },
      ret.data,
    )
  }

  async post(reqUser: any, path: string, values: any) {
    this.logger.debug!(
      `post: ${path}, values: ${JSON.stringify(values, null, 2)}, reqUser: ${JSON.stringify(reqUser, null, 2)}`,
    )
    const parsedPath = matchPath(path)
    if (parsedPath.length === 0) return Promise.resolve({})
    const { resource, id, resourceSchema } = parsedPath[parsedPath.length - 1]
    const def = await this.prisma.dynamicTableDef.findFirst({
      where: {
        name: resource,
        tenantId: reqUser.tid,
      },
    })
    if (def == null) {
      throw new Error(`cannot find table def of path: ${path}`)
    }
    const ret = await this.prisma.dynamicTableData.create({
      data: {
        dynamicTableDefId: def.id,
        data: values,
      },
    })
    return Object.assign(
      {
        id: ret.id,
      },
      ret.data,
    )
  }

  async remove(reqUser: any, path: string) {
    this.logger.debug!(`remove: ${path}, reqUser: ${JSON.stringify(reqUser, null, 2)}`)
    const parsedPath = matchPath(path)
    if (parsedPath.length === 0) return Promise.resolve({})
    const { id } = parsedPath[parsedPath.length - 1]
    const ret = await this.prisma.dynamicTableData.update({
      where: {
        id: id as number,
      },
      data: {
        isDeleted: true,
      },
    })
    return ret
  }
}
