import { parseEnv } from 'znv'
import { z } from 'zod'

export const FLOWDA_ENV = parseEnv(process.env, {
  TEST_ENV: z.string().optional(),
  DATABASE_URL: z.string().min(1),

  // 
  TENANT_REFRESH_TOKEN_SECRET: z.string().min(1),
  TENANT_REFRESH_TOKEN_EXPIRE: z.number().default(7 * 24 * 60 * 60), // 1 week
  TENANT_ACCESS_TOKEN_SECRET: z.string().min(1),
  TENANT_ACCESS_TOKEN_EXPIRE: z.number().default(24 * 60 * 60),

  // 
  REFRESH_TOKEN_SECRET: z.string().min(1),
  REFRESH_TOKEN_EXPIRE: z.number().default(7 * 24 * 60 * 60), // 1 week
  ACCESS_TOKEN_SECRET: z.string().min(1),
  ACCESS_TOKEN_EXPIRE: z.number().default(24 * 60 * 60),

  C7_REST_URL: z.string().min(1),

  // 
  SMS_SECRET_ID: z.string().min(1),
  SMS_SECRET_KEY: z.string().min(1),

  // 
  freecharger_pc_appid: z.string().min(1),
  freecharger_pc_appSecret: z.string().min(1),

  // 
  freecharger_fuwuhao_appid: z.string().min(1),
  freecharger_fuwuhao_secret: z.string().min(1),

  // 
  appid: z.string().min(1),
  mchid: z.string().min(1),
  'apiclient_cert.pem': z.string().min(1),
  'apiclient_key.pem': z.string().min(1),
})
