import { inject, injectable } from 'inversify'
import { TrpcService } from '../trpc.service'
import { type LoggerService } from '@nestjs/common'
import { createOrderSchema, createQuickOrderSchema, OrderV4Service } from '@flowda-projects/flowda-services'
import { z } from 'zod'

@injectable()
export class OrderV4Router {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(OrderV4Service) private orderV4: OrderV4Service,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(OrderV4Router.name)
  }

  router = this.trpc.router({
    query: this.trpc.userProtectedProcedure
      .input(
        z.object({
          orderId: z.number(),
        }),
      )
      .query(async ({ input }) => {
        return this.orderV4.query(input.orderId)
      }),
    queryPay: this.trpc.userProtectedProcedure
      .input(
        z.object({
          orderId: z.number(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.orderV4.queryPay(ctx.user.tenantId, ctx.user.id, input.orderId)
      }),
    createNative: this.trpc.userProtectedProcedure.input(createOrderSchema).mutation(async ({ input, ctx }) => {
      return this.orderV4.createNative({
        ...{
          tid: ctx.user.tenantId,
          uid: ctx.user.id,
        },
        ...input,
      })
    }),
    createJSAPI: this.trpc.userProtectedProcedure.input(createOrderSchema).mutation(async ({ input, ctx }) => {
      return this.orderV4.createJSAPI({
        ...{
          tid: ctx.user.tenantId,
          uid: ctx.user.id,
        },
        ...input,
      })
    }),
    createQuick: this.trpc.tenantProtectedProcedure.input(createQuickOrderSchema).mutation(async ({ input, ctx }) => {
      return this.orderV4.createQuick({
        ...{
          tid: ctx.tenant.id,
        },
        ...input,
      })
    }),
    queryPayQuick: this.trpc.tenantProtectedProcedure
      .input(
        z.object({
          orderId: z.number(),
          anonymousCustomerToken: z.string(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.orderV4.queryPayQuick(ctx.tenant.id, input.anonymousCustomerToken, input.orderId)
      }),
  })
}
