import { inject, injectable } from 'inversify'
import { APISymbol, PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import * as db from '@prisma/client-flowda'
import type { AxiosInstance } from 'axios'
import { lowerFirst } from 'lodash'
import { FLOWDA_ENV } from '../lib/flowda-env'
import { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import { createZodDto } from 'nestjs-zod'

const StartSchema = z.object({
  tenantId: z.string(),
  processDefKey: z.string(),
  businessKey: z.union([z.number(), z.string()]),
})

export class StartSchemaDto extends createZodDto(StartSchema) {}

const GetTaskFormSchema = z.object({
  taskId: z.string(),
})

export class GetTaskFormSchemaDto extends createZodDto(GetTaskFormSchema) {}

@injectable()
export class TaskService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(APISymbol) private API: AxiosInstance,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(TaskService.name)
  }

  async start(dto: StartSchemaDto, reqUser: any) {
    const res = await this.API.post(
      FLOWDA_ENV.C7_REST_URL + `/process-definition/key/${dto.processDefKey}/tenant-id/${dto.tenantId}/start`,
      {
        variables: {
          uid: {
            value: reqUser.uid,
            type: 'Integer',
          },
        },
        businessKey: dto.businessKey,
      },
    )
  }

  async getTask(taskId: string) {
    const res = await this.API.get(FLOWDA_ENV.C7_REST_URL + `/task/${taskId}`)
    const res2 = await this.API.get(FLOWDA_ENV.C7_REST_URL + `/task/${taskId}/form-variables`)
    res.data.variables = JSON.stringify(res2.data)
    return res.data
  }

  async complete(taskId: string) {
    try {
      const ret2 = await this.API.post(
        FLOWDA_ENV.C7_REST_URL + `/task/${taskId}/complete`,
        {},
        {
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
        },
      )
    } catch (e) {
      this.logger.error(e)
    }

    return {
      taskId,
    }
  }

  /**
   * 1.  taskId -> formKey
   * 2.  formKey -> form schema  view model
   * 3.  form schema  onInit  onComplete
   */
  async getTaskForm(dto: GetTaskFormSchemaDto) {
    try {
      const res = await this.API.get(FLOWDA_ENV.C7_REST_URL + `/task/${dto.taskId}`)
      const taskDefinitionKey = res.data.taskDefinitionKey
      const processInstanceId = res.data.processInstanceId
      const res3 = await this.API.get(FLOWDA_ENV.C7_REST_URL + `/process-instance/${processInstanceId}`)
      const res2 = await this.API.get(FLOWDA_ENV.C7_REST_URL + `/task/${dto.taskId}/form-variables`)
      this.logger.debug!(`getTaskForm(${JSON.stringify(dto)}): ${JSON.stringify(res2.data, null, 2)}`)
      const ret = await this.prisma.taskFormRelation.findUnique({
        where: {
          taskDefinitionKey,
        },
      })
      return {
        taskFormRelation: ret,
        process: res3.data,
        task: res.data,
        variables: res2.data,
      }
    } catch (e: any) {
      this.logger.error(`[getTaskForm] error ${e.message}`)
      throw new Error(e)
    }
  }

  async completeResource(taskId: string, body: any) {
    const { resource, data } = body
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const ret1 = await this.prisma[lowerFirst(resource)].create({
      data: data,
    })
    try {
      const ret2 = await this.API.post(
        FLOWDA_ENV.C7_REST_URL + `/task/${taskId}/complete`,
        {},
        {
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
        },
      )
    } catch (e) {
      this.logger.error(e)
    }

    return {
      taskId,
      resource,
      data,
      ret1,
    }
  }
}
