import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import * as db from '@prisma/client-flowda'

const tenantMenu = [
  {
    id: 'tenant_admin',
    name: '',
    slug: 'tenant_admin',
    children: [
      {
        name: '',
        slug: 'tenant',
        key: 'tenant',
        children: [
          { id: 'tenants', name: '', slug: 'tenants' },
          { id: 'menus', name: '', slug: 'menus' },
        ],
      },
      {
        name: '',
        slug: 'dynamic_table',
        id: 'dynamic_table',
        children: [{ id: 'dynamic_table_defs', name: '', slug: 'dynamic_table_defs' }],
      },
    ],
  },
]

@injectable()
export class MenuService {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(MenuService.name)
  }

  async get(reqUser: any) {
    if (reqUser.tid == null) {
      throw new Error('No tenant id')
    }
    const tenantRet = await this.prisma.tenant.findUnique({
      where: {
        id: reqUser.tid,
      },
    })
    if (tenantRet == null) {
      throw new Error('No tenant')
    }

    const menuRet = await this.prisma.menu.findUnique({
      where: {
        tenantId: reqUser.tid,
      },
    })

    if (tenantRet.name === 'superadmin') {
      if (menuRet && Array.isArray(menuRet.treeData)) {
        return menuRet.treeData.concat(tenantMenu)
      } else {
        return tenantMenu
      }
    } else {
      return menuRet?.treeData || []
    }
  }
}
