import { FLOWDA_ENV } from '../lib/flowda-env'
import { ConflictException, ForbiddenException, LoggerService, UnauthorizedException } from '@nestjs/common'
import dayjs from 'dayjs'
import type { Client } from 'tencentcloud-sdk-nodejs/tencentcloud/services/sms/v20210111/sms_client'
import { WeixinLoginService } from './weixin-login.service'
import * as _ from 'radash'
import { WeixinFuwuhaoLoginService } from './weixin-fuwuhao-login.service'
import { diag } from '@flowda-projects/flowda-shared-node'

export const tenantSelect = db.Prisma.validator<db.Prisma.TenantSelect>()({
  id: true,
  name: true,
  displayName: true,
  hashedRefreshToken: true,
  hashedPassword: true,
})

// https://www.prisma.io/docs/orm/prisma-client/type-safety/prisma-validator
//  `userSelect: db.Prisma.UserSelect`  UserSelect  property  `?` 
export const userSelect = db.Prisma.validator<db.Prisma.UserSelect>()({
  id: true,
  username: true,
  email: true,
  image: true,
  hashedPassword: true,
  hashedRefreshToken: true,
  tenantId: true,
  tenant: {
    select: {
      name: true,
    },
  },
  orderProfile: {
    select: {
      productType: true,
      plan: true,
      amount: true,
      expireAt: true,
    },
  },
  weixinProfile: {
    select: {
      unionid: true,
      loginOpenid: true,
      headimgurl: true,
      nickname: true,
      sex: true,
    },
  },
})

@injectable()
export class UserService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(SmsClientSymbol) private smsClient: Client,
    @inject(WeixinLoginService) private weixinLogin: WeixinLoginService,
    @inject(WeixinFuwuhaoLoginService) private weixinFuwuhaoLogin: WeixinFuwuhaoLoginService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserService.name)
  }

  async register(dto: RegisterDto) {
    const user = await this.prisma.user.findFirst({
      where: {
        username: dto.username,
      },
    })
    if (user) {
      throw new ConflictException(`User exists`)
    }
    const hashedPassword = await bcrypt.hash(dto.password, 10)
    const aUser = await this.prisma.user.create({
      data: {
        username: dto.username,
        hashedPassword: hashedPassword,
        hashedRefreshToken: null,
        tenantId: dto.tenantId,
      },
    })
    return {
      id: aUser.id,
      username: aUser.username,
    }
  }

  async resetPassword(reqUser: any, dto: resetPasswordSchemaDto) {
    const tenantRet = await this.prisma.tenant.findUniqueOrThrow({
      where: {
        id: reqUser.tid,
      },
    })
    if (tenantRet.name !== 'superadmin') {
      throw new Error('Not allowed')
    }

    await this.prisma.user.findFirstOrThrow({
      where: {
        id: dto.userId,
        tenantId: dto.tenantId,
      },
    })
    const hashedPassword = await bcrypt.hash(dto.password, 10)
    return this.prisma.user.update({
      where: {
        id: dto.userId,
      },
      data: {
        hashedPassword,
      },
    })
  }

  async validateByEmail(input: validateByEmailSchemaDto, ctx?: TCtx) {
    diag(ctx, '[validateByEmail]')
    const { tenantId, email, password } = input
    const userRet = await this.prisma.user.findFirstOrThrow({
      where: {
        email: email,
        tenantId: tenantId,
      },
      select: {
        id: true,
        username: true,
      },
    })
    diag(ctx, 'user.findFirstOrThrow', userRet)
    return this.validate({ username: userRet.username, password }, ctx)
  }

  async validate(input: validateSchemaDto, ctx?: TCtx) {
    diag(ctx, '[validate]')
    const { username, password } = input
    const userRet = await this.prisma.user.findFirst({
      where: {
        username: username,
      },
      select: userSelect,
    })
    diag(ctx, 'user.findFirst', userRet)
    if (!userRet) {
      // https://docs.nestjs.com/exception-filters#built-in-http-exceptions
      throw new UnauthorizedException(`User does not exist`)
    }

    if (!userRet.hashedPassword) {
      throw new UnauthorizedException(`Password is not initialized`)
    }

    const match = await bcrypt.compare(password, userRet.hashedPassword)
    if (!match) {
      throw new UnauthorizedException(`Username and password is not matched`)
    }

    const payload = { uid: userRet.id, tid: userRet.tenantId }
    const rtRet = await generateUserRefreshToken(payload)
    diag(ctx, 'generateUserRefreshToken', rtRet)

    await this.prisma.user.update({
      where: { id: userRet.id },
      data: {
        hashedRefreshToken: rtRet.hash,
      },
    })
    diag(ctx, 'user.update', rtRet.hash)

    const at = generateJwt(payload, {
      secret: FLOWDA_ENV.ACCESS_TOKEN_SECRET,
      expires: FLOWDA_ENV.ACCESS_TOKEN_EXPIRE,
    })

    this.logger.log(
      `validate pass, tid:${userRet.tenantId}, u:${userRet.username}, e:${dayjs().add(
        FLOWDA_ENV.ACCESS_TOKEN_EXPIRE,
        's',
      )}`,
    )

    return {
      user: _.omit(userRet, ['hashedRefreshToken', 'hashedPassword']),
      rt: rtRet.rt,
      at,
    }
  }

  async validateTenant(dto: validateTenantSchemaDto, ctx?: TCtx) {
    const { name, password } = dto
    const tenant = await this.prisma.tenant.findFirst({
      where: {
        name: name,
      },
      select: tenantSelect,
    })
    diag(ctx, '(e)tenant.findFirst', tenant)

    if (tenant == null) {
      throw new UnauthorizedException(`tenant does not exist`)
    }

    if (!tenant.hashedPassword) {
      throw new UnauthorizedException(`tenant password is not initialized`)
    }

    const match = await bcrypt.compare(password, tenant.hashedPassword)
    if (!match) {
      throw new UnauthorizedException(`tenant name and password is not matched`)
    }

    const payload = { tid: tenant.id }
    const rtRet = await generateTenantRefreshToken(payload)
    diag(ctx, '(e) generateTenantRefreshToken', payload)
    await this.prisma.tenant.update({
      where: { id: tenant.id },
      data: {
        hashedRefreshToken: rtRet.hash,
      },
    })

    const at = generateJwt(payload, {
      secret: FLOWDA_ENV.TENANT_ACCESS_TOKEN_SECRET,
      expires: FLOWDA_ENV.TENANT_ACCESS_TOKEN_EXPIRE,
    })
    this.logger.log(`validate pass, t: ${tenant.name}, e: ${dayjs().add(FLOWDA_ENV.TENANT_ACCESS_TOKEN_EXPIRE, 's')}`)

    return {
      tenant: _.omit(tenant, ['hashedPassword', 'hashedRefreshToken']),
      rt: rtRet.rt,
      at,
    }
  }

  async refreshTenantToken(rt: string, ctx?: TCtx) {
    diag(ctx, '[refreshTenantToken]')
    const decodedToken = jwt.verify(rt, FLOWDA_ENV.TENANT_REFRESH_TOKEN_SECRET) as JwtPayload
    const tid = decodedToken['tid']
    diag(ctx, 'jwt.verify', decodedToken)
    const tenant = await this.prisma.tenant.findUniqueOrThrow({
      where: {
        id: tid,
      },
      select: tenantSelect,
    })
    diag(ctx, 'tenant.findUniqueOrThrow', tenant)
    if (tenant.hashedRefreshToken == null) {
      throw new UnauthorizedException('tenant refresh token not exists')
    }
    const match = await bcrypt.compare(rt, tenant.hashedRefreshToken)
    if (!match) {
      throw new UnauthorizedException('tenant refresh token is invalid')
    }
    const at = generateJwt(
      { tid },
      {
        secret: FLOWDA_ENV.TENANT_ACCESS_TOKEN_SECRET,
        expires: FLOWDA_ENV.TENANT_ACCESS_TOKEN_EXPIRE,
      },
    )
    return {
      at,
      tenant: _.omit(tenant, ['hashedRefreshToken', 'hashedPassword']),
    }
  }

  verifyAccessToken(at: string) {
    return jwt.verify(at, FLOWDA_ENV.ACCESS_TOKEN_SECRET) as JwtPayload
  }

  verifyTenantAccessToken(at: string) {
    return jwt.verify(at, FLOWDA_ENV.TENANT_ACCESS_TOKEN_SECRET) as JwtPayload
  }

  async getUserInfo(uid: number, ctx?: TCtx) {
    diag(ctx, '[getUserInfo]')
    const ret = await this.prisma.user.findUniqueOrThrow({
      where: {
        id: uid,
      },
      select: userSelect,
    })
    diag(ctx, 'user found', ret)
    return _.omit(ret, ['hashedRefreshToken', 'hashedPassword'])
  }

  async getTenantInfo(tid: number, ctx?: TCtx) {
    const ret = await this.prisma.tenant.findUniqueOrThrow({
      where: {
        id: tid,
      },
      select: tenantSelect,
    })
    return _.omit(ret, ['hashedRefreshToken', 'hashedPassword'])
  }

  async sendSmsVerifyCode(input: sendSmsVerifyCodeSchemaDto, ctx?: TCtx) {
    diag(ctx, ['sendSmsVerifyCode'])
    const code = generateVerificationCode()
    diag(ctx, 'generateVerificationCode', { code })
    try {
      const res = await this.smsClient.SendSms({
        SmsSdkAppId: '1400886368',
        PhoneNumberSet: ['+86' + input.mobile],
        TemplateId: '2062585',
        SignName: '',
        TemplateParamSet: [code],
      })
      diag(ctx, 'smsClient.SendSms', res)
    } catch (e: any) {
      throw new Error(e)
    }
    return this.prisma.sentSms.create({
      data: {
        mobile: input.mobile,
        code: code,
      },
    })
  }

  async verifyMobile(dto: verifyMobileSchemaDto, ctx?: TCtx) {
    diag(ctx, '[verifyMobile]')
    const now = dayjs()
    const tenMinutesAgo = now.subtract(10, 'minute')
    await this.prisma.sentSms.findFirstOrThrow({
      where: {
        code: dto.code,
        mobile: dto.mobile,
        createdAt: {
          gte: tenMinutesAgo.toDate(),
          lte: now.toDate(),
        },
      },
    })
    diag(ctx, 'called sentSms.findFirstOrThrow')
    await this.prisma.user.findFirstOrThrow({
      where: {
        id: dto.uid,
        tenantId: dto.tid,
      },
    })
    diag(ctx, 'called user.findFirstOrThrow')
    const ret = await this.prisma.user.update({
      where: {
        id: dto.uid,
      },
      data: {
        mobile: dto.mobile,
      },
      select: {
        id: true,
        tenantId: true,
      },
    })
    diag(ctx, 'called user.update')
    return ret
  }

  getTenantByName(dto: GetTenantByNameSchemaDto) {
    return this.prisma.tenant.findFirstOrThrow({
      where: {
        name: dto.tenantName,
      },
    })
  }

  async accountExists(dto: AccountExistsSchemaDto) {
    const ret = await this.prisma.user.findFirstOrThrow({
      where: {
        username: dto.username,
        tenant: {
          name: dto.tenantName,
        },
      },
    })
    return ret
  }

  async findByUnionIdAndTenantId(dto: FindByUnionIdAndTenantIdSchemaDto) {
    const ret = await this.prisma.user.findFirst({
      where: {
        weixinProfile: {
          unionid: dto.unionid,
        },
        tenantId: dto.tenantId,
      },
    })
    return ret
  }

  async registerByUnionId(dto: RegisterByUnionIdSchemaDto, ctx?: TCtx) {
    const user = await this.prisma.user.findFirst({
      where: {
        weixinProfile: {
          unionid: dto.unionid,
        },
        tenantId: dto.tenantId,
      },
    })
    diag(ctx, 'user.findFirst', user)
    if (user) {
      throw new Error(`User exits with unionid: ${dto.unionid}`)
    }

    const wxProfileRet = await this.prisma.weixinProfile.upsert({
      where: {
        unionOrOpenid: dto.unionid,
      },
      create: {
        unionOrOpenid: dto.unionid,
        unionid: dto.unionid,
      },
      update: {
        unionOrOpenid: dto.unionid,
        unionid: dto.unionid,
      },
    })
    diag(ctx, 'weixinProfile.upsert', wxProfileRet)

    const aUser = await this.prisma.user.create({
      data: {
        username: dto.unionid, //  unionid === username 
        hashedPassword: null,
        hashedRefreshToken: null,
        weixinProfileId: wxProfileRet.id,
        tenantId: dto.tenantId,
      },
    })
    return aUser
  }

  async wxValidateUser(dto: wxValidateUserTenantJwtPayloadSchemaDto, ctx?: TCtx) {
    diag(ctx, '[wxValidateUser]', dto)

    const { tid, code } = dto

    diag(ctx, 'call weixinLogin.getAccessToken', code)
    const wxRet = await this.weixinLogin.getAccessToken(code)
    diag(ctx, 'called weixinLogin.getAccessToken', wxRet)

    const wxUser = await this.weixinLogin.getUser(wxRet.data.openid, wxRet.data.access_token)
    diag(ctx, 'called weixinLogin.getUser', wxUser)

    const wxProfileRet = await this.prisma.weixinProfile.upsert({
      where: {
        unionOrOpenid: wxRet.data.unionid,
      },
      create: {
        unionOrOpenid: wxRet.data.unionid,
        unionid: wxRet.data.unionid,
        headimgurl: wxUser.headimgurl,
        nickname: wxUser.nickname,
        sex: wxUser.sex,
      },
      update: {
        unionOrOpenid: wxRet.data.unionid,
        unionid: wxRet.data.unionid,
        headimgurl: wxUser.headimgurl,
        nickname: wxUser.nickname,
        sex: wxUser.sex,
      },
    })
    diag(ctx, 'called weixinProfile.upsert', wxProfileRet)

    diag(ctx, 'call user.findFirst', {
      unionid: wxRet.data.unionid,
      tenantId: tid,
    })
    let userRet = await this.prisma.user.findFirst({
      where: {
        username: wxRet.data.unionid,
        tenantId: tid,
      },
      select: userSelect,
    })
    diag(ctx, 'userRet', userRet)

    if (!userRet /* */) {
      diag(ctx, 'wxProfileRet upsert', wxProfileRet)
      userRet = await this.prisma.user.create({
        data: {
          username: wxRet.data.unionid, //  unionid === username 
          hashedPassword: null,
          hashedRefreshToken: null,
          tenantId: tid,
          weixinProfileId: wxProfileRet.id,
        },
        select: userSelect,
      })
      diag(ctx, 'create userRet', userRet)
    }

    const payload = { uid: userRet.id, tid: userRet.tenantId }
    const at = generateJwt(payload, {
      secret: FLOWDA_ENV.ACCESS_TOKEN_SECRET,
      expires: FLOWDA_ENV.ACCESS_TOKEN_EXPIRE,
    })
    const rtRet = await generateUserRefreshToken(payload)
    await this.prisma.user.update({
      where: { id: userRet.id },
      data: {
        weixinProfileId: wxProfileRet.id,
        hashedRefreshToken: rtRet.hash,
      },
    })
    diag(ctx, 'update rt')

    return {
      at: at,
      rt: rtRet.rt,
      user: _.omit(userRet, ['hashedRefreshToken', 'hashedPassword']),
    }
  }

  async refreshToken(input: refreshTokenSchemaDto, ctx?: TCtx) {
    diag(ctx, '[refreshToken]')
    const { rt } = input
    const decodedToken = jwt.verify(rt, FLOWDA_ENV.REFRESH_TOKEN_SECRET) as JwtPayload
    diag(ctx, 'jwt.verify', decodedToken)
    const userRet = await this.prisma.user.findUniqueOrThrow({
      where: {
        id: decodedToken['uid'],
      },
      select: userSelect,
    })
    diag(ctx, 'user.findUniqueOrThrow', userRet)
    if (userRet.hashedRefreshToken == null) {
      throw new UnauthorizedException(`user refresh token not exists`)
    }
    const match = await bcrypt.compare(rt, userRet.hashedRefreshToken)
    if (!match) {
      throw new UnauthorizedException('user refresh token is invalid')
    }
    const payload = { tid: userRet.tenantId, uid: userRet.id }
    diag(ctx, 'generateJwt', payload)
    const at = generateJwt(payload, {
      secret: FLOWDA_ENV.ACCESS_TOKEN_SECRET,
      expires: FLOWDA_ENV.ACCESS_TOKEN_EXPIRE,
    })
    return {
      at,
      user: _.omit(userRet, ['hashedRefreshToken', 'hashedPassword']),
    }
  }

  async generateRecoveryCode(dto: generateRecoveryCodeTenantJwtSchemaDto, ctx?: TCtx) {
    diag(ctx, '[generateRecoveryCode]')
    const userRet = await this.prisma.user.findUniqueOrThrow({
      where: {
        email: dto.email,
      },
    })
    diag(ctx, 'user.findUniqueOrThrow', userRet)
    const recoveryToken = generateRecoveryToken(dto.email, {
      secret: FLOWDA_ENV.ACCESS_TOKEN_SECRET,
    })
    // token  code 
    const recoveryCode = _.uid(6).toUpperCase()
    diag(ctx, 'generateRecoveryToken', { recoveryToken, recoveryCode })
    await this.prisma.user.update({
      where: {
        id: userRet.id,
      },
      data: {
        recoveryToken,
        recoveryCode,
      },
    })
    // todo: 
    return {
      recoveryCode,
    }
  }

  async resetPasswordWithRecoveryCode(dto: resetPasswordWithRecoveryCodeTenantJwtSchemaDto, ctx?: TCtx) {
    diag(ctx, '[resetPasswordWithRecoveryCode]')
    const userRet = await this.prisma.user.findFirstOrThrow({
      where: {
        tenantId: dto.tid,
        recoveryCode: dto.recoveryCode,
      },
    })
    diag(ctx, 'user.findFirstOrThrow', userRet)
    if (userRet.recoveryToken == null) {
      throw new ForbiddenException(`invalid recovery code`)
    }
    const decodeToken = verifyRecoveryToken(userRet.recoveryToken, { secret: FLOWDA_ENV.ACCESS_TOKEN_SECRET })
    diag(ctx, 'decodeToken', decodeToken)
    if (!decodeToken['verificationToken']) {
      throw new ForbiddenException('invalid recovery token')
    }
    const hash = await bcrypt.hash(dto.password, 10)
    const ret = await this.prisma.user.update({
      where: {
        id: userRet.id,
      },
      data: {
        hashedPassword: hash,
        hashedRefreshToken: null,
        recoveryCode: null,
        recoveryToken: null,
      },
      select: userSelect,
    })
    return _.omit(ret, ['hashedRefreshToken', 'hashedPassword'])
  }

  async fwhLogin(dto: fwhLoginTenantJwtPayloadSchemaDto) {
    const ret = await this.weixinFuwuhaoLogin.getAccessTokenByCode(dto.code)
    const name = ret.unionid || ret.openid //  unionid  fallback  openid
    const wxUser = await this.weixinLogin.getUser(ret.openid, ret.access_token)
    const wxProfileRet = await this.prisma.weixinProfile.upsert({
      where: {
        unionOrOpenid: ret.unionid || ret.openid,
      },
      create: {
        unionOrOpenid: ret.unionid || ret.openid,
        unionid: ret.unionid,
        loginOpenid: ret.openid,
        headimgurl: wxUser.headimgurl,
        nickname: wxUser.nickname,
        sex: wxUser.sex,
      },
      update: {
        unionOrOpenid: ret.unionid || ret.openid,
        unionid: ret.unionid,
        loginOpenid: ret.openid,
        headimgurl: wxUser.headimgurl,
        nickname: wxUser.nickname,
        sex: wxUser.sex,
      },
    })
    let userRet = await this.prisma.user.findFirst({
      where: {
        username: name,
        tenantId: dto.tid,
      },
      select: userSelect,
    })

    let rt: { token: string; iat: number; exp: number }
    if (!userRet /*  */) {
      userRet = await this.prisma.user.create({
        data: {
          username: name,
          tenantId: dto.tid,
          // hashedRefreshToken: hash, // todo:  hash
          weixinProfileId: wxProfileRet.id,
        },
        select: userSelect,
      })
      const rtRet = await generateUserRefreshToken({
        uid: userRet.id,
        tid: userRet.tenantId,
      })
      rt = rtRet.rt
      await this.prisma.user.update({
        where: {
          id: userRet.id,
        },
        data: {
          hashedRefreshToken: rtRet.hash,
        },
      })
    } /*  profile */ else {
      const rtRet = await generateUserRefreshToken({
        uid: userRet.id,
        tid: userRet.tenantId,
      })
      rt = rtRet.rt
      userRet = await this.prisma.user.update({
        where: {
          id: userRet.id,
        },
        select: userSelect,
        data: {
          hashedRefreshToken: rtRet.hash,
          weixinProfileId: wxProfileRet.id,
        },
      })
    }

    const payload = { uid: userRet.id, tid: userRet.tenantId }

    const at = generateJwt(payload, {
      secret: FLOWDA_ENV.ACCESS_TOKEN_SECRET,
      expires: FLOWDA_ENV.ACCESS_TOKEN_EXPIRE,
    })

    return {
      at,
      rt,
      user: _.omit(userRet, ['hashedRefreshToken', 'hashedPassword']),
    }
  }

  async createTenant(input: appCreateV4SchemaDto, ctx?: TCtx) {
    diag(ctx, '[createTenant]')
    const randomAppId = _.uid(4).toUpperCase()
    const randomAppToken = _.uid(24)
    const hashedPassword = await bcrypt.hash(randomAppToken, 10)
    diag(ctx, 'tenant.create params', { randomAppId, randomAppToken })
    const ret = await this.prisma.tenant.create({
      data: {
        name: randomAppId,
        displayName: input.displayName,
        hashedPassword,
      },
      select: tenantSelect,
    })
    diag(ctx, 'tenant.create')
    // appToken 
    return Object.assign({ appToken: randomAppToken }, _.omit(ret, ['hashedRefreshToken', 'hashedPassword']))
  }

  // todo:  
  async _resetTenantPassword(input: _resetTenantPasswordSchemaDto) {
    const randomAppToken = _.uid(24)
    const hashedPassword = await bcrypt.hash(randomAppToken, 10)
    const ret = await this.prisma.tenant.update({
      where: {
        id: input.id,
      },
      data: {
        hashedPassword,
      },
      select: tenantSelect,
    })
    // appToken 
    return Object.assign({ appToken: randomAppToken }, _.omit(ret, ['hashedRefreshToken', 'hashedPassword']))
  }

  async preSignup(input: customerPreSignupTenantJwtPayloadSchemaDto, ctx?: TCtx) {
    diag(ctx, '[preSignup]')
    const user = await this.prisma.user.findUnique({
      where: { email: input.email },
    })
    if (user) {
      // https://docs.nestjs.com/exception-filters#built-in-http-exceptions
      throw new ConflictException('Email already exists', {
        description: `userId:${user.id}`,
      })
    }
    const randomCode = _.uid(6).toUpperCase()
    diag(ctx, 'verifyCode', { randomCode })
    await this.prisma.userPreSignup.create({
      data: {
        tenantId: input.tid,
        email: input.email,
        verifyCode: randomCode,
      },
    })
    // todo: 
    return {
      verifyCode: randomCode,
    }
  }

  async verifyAndSignup(input: customerSignupTenantJwtPayloadSchemaDto, ctx?: TCtx) {
    diag(ctx, '[verifyAndSignup]')
    const ret = await this.prisma.userPreSignup.findFirstOrThrow({
      where: {
        tenantId: input.tid,
        email: input.email,
        verifyCode: input.verifyCode,
        isDeleted: false,
      },
    })
    diag(ctx, 'userPreSignup found', ret)
    const hashedPassword = await bcrypt.hash(input.password, 10)

    const [userRet] = await this.prisma.$transaction([
      this.prisma.user.create({
        data: {
          username: input.email, //  email === username 
          email: input.email,
          hashedPassword: hashedPassword,
          hashedRefreshToken: null,
          tenantId: input.tid,
        },
        select: userSelect,
      }),
      this.prisma.userPreSignup.update({
        where: {
          id: ret.id,
        },
        data: {
          isDeleted: true,
        },
      }),
    ])
    diag(ctx, '$transaction complete', userRet)
    return _.omit(userRet, ['hashedRefreshToken', 'hashedPassword'])
  }

  async amountUpdate(input: amountUpdateUserJwtPayloadSchemaDto) {
    const profileRet = await this.prisma.orderProfile.findUniqueOrThrow({
      where: {
        userId: input.uid,
      },
    })
    if (profileRet.expireAt && new Date(profileRet.expireAt).valueOf() < Date.now()) {
      throw new ForbiddenException('Order profile is expired', {
        description: `expired at ${profileRet.expireAt}`,
      })
    }
    const count = input.count || 1
    const action = input.action || 'decrement'
    if (action === 'decrement' && (profileRet.amount == null || profileRet.amount < count)) {
      throw new ForbiddenException('Action not allowed', {
        description: `profile amount ${profileRet.amount} less than ${count}`,
      })
    }

    const ret = await this.prisma.user.update({
      where: {
        id: profileRet.userId,
      },
      select: userSelect,
      data: {
        orderProfile: {
          update: {
            amount: {
              decrement: count,
            },
          },
        },
      },
    })
    return _.omit(ret, ['hashedRefreshToken', 'hashedPassword'])
  }

  async createQuick(input: createQuickOrderTenantJWTPayloadSchemaDto) {
    const userRet = await this.prisma.user.findFirst({
      where: {
        username: input.anonymousCustomerToken,
        tenantId: input.tid,
      },
    })
    if (userRet) {
      throw new ConflictException('')
    }
    return this.prisma.user.create({
      data: {
        tenantId: input.tid,
        username: input.anonymousCustomerToken,
        anonymousCustomerToken: input.anonymousCustomerToken,
      },
      select: userSelect,
    })
  }

  async logout(dto: userJwtPayloadSchemaDto) {
    await this.prisma.user.update({
      where: {
        id: dto.uid,
      },
      data: {
        hashedRefreshToken: null,
      },
    })
    return {}
  }
}

export function generateVerificationCode() {
  let verificationCode = ''
  const digits = '0123456789'

  while (verificationCode.length < 6) {
    const randomDigit = digits[Math.floor(Math.random() * digits.length)]
    if (!verificationCode.includes(randomDigit)) {
      verificationCode += randomDigit
    }
  }

  return verificationCode
}

export function generateRecoveryToken(email: string, options: { secret: string }): string {
  const recoveryToken = jwt.sign(
    {
      email: email,
      verificationToken: true,
      exp: Math.floor(Date.now() / 1000) + 30 * 60, // 30 
    },
    options.secret,
  )
  return recoveryToken
}

function verifyRecoveryToken(at: string, options: { secret: string }): JwtPayload {
  return jwt.verify(at, options.secret) as JwtPayload
}

function generateJwt(payload: any, options: { secret: string; expires: number }) {
  const exp0 = Date.now() + options.expires * 1000
  const token = jwt.sign(
    {
      ...payload,
      ...{ exp0 },
    },
    options.secret,
    {
      expiresIn: `${options.expires}s`,
    },
  )
  const decode = jwt.decode(token) as JwtPayload
  return {
    token: token,
    iat: decode.iat as number,
    exp: decode.exp as number,
  }
}

async function generateUserRefreshToken(payload: { uid: number; tid: number }) {
  const rt = generateJwt(payload, {
    secret: FLOWDA_ENV.REFRESH_TOKEN_SECRET,
    expires: FLOWDA_ENV.REFRESH_TOKEN_EXPIRE,
  })
  const hash = await bcrypt.hash(rt.token, 10)
  return {
    rt,
    hash,
  }
}

async function generateTenantRefreshToken(payload: { tid: number }) {
  const rt = generateJwt(payload, {
    secret: FLOWDA_ENV.TENANT_REFRESH_TOKEN_SECRET,
    expires: FLOWDA_ENV.TENANT_REFRESH_TOKEN_EXPIRE,
  })
  const hash = await bcrypt.hash(rt.token, 10)
  return {
    rt,
    hash,
  }
}
