import { inject, injectable } from 'inversify'
import { TrpcService } from '../trpc.service'
import { type LoggerService } from '@nestjs/common'
import { PrismaClientSymbol, productCreateManyItemSchema } from '@flowda-projects/flowda-shared-types'
import { ProductV4Service } from '@flowda-projects/flowda-services'
import { z } from 'zod'
import * as db from '@prisma/client-flowda'

@injectable()
export class ProductV4Router {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(ProductV4Service) private productV4: ProductV4Service,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProductV4Router.name)
  }

  router = this.trpc.router({
    createManyProducts: this.trpc.tenantProtectedProcedure
      .input(z.array(productCreateManyItemSchema))
      .mutation(async ({ input, ctx }) => {
        return this.productV4.createManyProducts(ctx.tenant.id, input)
      }),

    findAll: this.trpc.tenantProtectedProcedure.input(z.object({})).query(async ({ input, ctx }) => {
      return this.prisma.product.findMany({
        where: {
          tenantId: ctx.tenant.id,
        },
      })
    }),
  })
}
