import { inject, injectable } from 'inversify'
import axios from 'axios'
import { InternalServerErrorException, LoggerService } from '@nestjs/common'
import { FLOWDA_ENV } from '../lib/flowda-env'
import { WechatOAuthSymbol } from '@flowda-projects/flowda-shared-types'
import WechatOAuth from 'wechat-oauth'

@injectable()
export class WeixinFuwuhaoLoginService {
  protected readonly logger
  constructor(
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(WeixinFuwuhaoLoginService.name)
  }
  async getAccessToken(): Promise<{ access_token: string; expires_in: number }> {
    const appid = FLOWDA_ENV.freecharger_fuwuhao_appid
    const secret = FLOWDA_ENV.freecharger_fuwuhao_secret
    // https://github.com/axios/axios/issues/5082
    const ret = await axios({
      method: 'get',
      url: `https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid=${appid}&secret=${secret}`,
    })
    return ret.data
  }

  async getAccessTokenByCode(code: string) {
    const appid = FLOWDA_ENV.freecharger_fuwuhao_appid
    const secret = FLOWDA_ENV.freecharger_fuwuhao_secret
    const res = await axios({
      method: 'get',
      url: `https://api.weixin.qq.com/sns/oauth2/access_token?appid=${appid}&secret=${secret}&code=${code}&grant_type=authorization_code`,
    })
    if (res.data.errcode) {
      console.log('get access token error: ', JSON.stringify(res.data))
      throw new InternalServerErrorException(' access token ', {
        description: res.data.errcode,
      })
    }
    return res.data
  }
}
