/// <reference types="@types/express-serve-static-core" />

import * as trpcExpress from '@trpc/server/adapters/express'
import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import {
  ctxTenantSchema,
  ctxTenantSchemaDto,
  ctxUserSchema,
  ctxUserSchemaDto,
  UserService,
} from '@flowda-projects/flowda-services'
import { createContext } from '@flowda-projects/flowda-shared-node'

@injectable()
export class ContextFactory {
  private readonly logger

  constructor(
    @inject(UserService) private userService: UserService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ContextFactory.name)
  }

  createContext = async (opts: trpcExpress.CreateExpressContextOptions) => {
    let tenant: ctxTenantSchemaDto | undefined
    let user: ctxUserSchemaDto | undefined

    const ctx = createContext(opts)
    const tenantAt =
      typeof opts.req.headers['tenant-authorization'] === 'string'
        ? opts.req.headers['tenant-authorization'].split(' ')[1]
        : null
    if (tenantAt) {
      const { tid } = this.userService.verifyTenantAccessToken(tenantAt)
      const tenantRet = await this.userService.getTenantInfo(tid)
      tenant = ctxTenantSchema.parse(tenantRet)
    }

    const at =
      typeof ctx.req.headers['authorization'] === 'string' ? ctx.req.headers['authorization'].split(' ')[1] : null
    if (at) {
      const { uid } = this.userService.verifyAccessToken(at)
      const userRet = await this.userService.getUserInfo(uid, ctx)
      user = ctxUserSchema.parse(userRet)
    }

    return {
      ...ctx,
      ...{ user, tenant },
    }
  }
}

export type Context = Awaited<ReturnType<typeof ContextFactory.prototype.createContext>>
