import { inject, injectable } from 'inversify'
import { TWxGetAccessTokenRes, TWxGetUser, WechatOAuthSymbol } from '@flowda-projects/flowda-shared-types'
import WechatOAuth from 'wechat-oauth'
import { LoggerService } from '@nestjs/common'

@injectable()
export class WeixinLoginService {
  protected readonly logger

  constructor(
    @inject(WechatOAuthSymbol) private wechatOAuth: WechatOAuth,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(WeixinLoginService.name)
  }

  getAuthorizeURLForWebsite(redirectUrl: string) {
    const url = this.wechatOAuth.getAuthorizeURLForWebsite(redirectUrl)
    return url
  }

  getAccessToken(code: string): Promise<{ data: TWxGetAccessTokenRes }> {
    return new Promise((resolve, reject) => {
      this.wechatOAuth.getAccessToken(code, function (err: any, result: any) {
        if (err) {
          reject(err)
        } else {
          resolve(result)
        }
      })
    })
  }

  getUser(openid: string, access_token: string): Promise<TWxGetUser> {
    return new Promise((resolve, reject) => {
      this.wechatOAuth._getUser(
        {
          openid,
        },
        access_token,
        function (err: any, result: any) {
          if (err) {
            reject(err)
          } else {
            resolve(result)
          }
        },
      )
    })
  }
}
