import { Body, Controller, Get, Param, Post } from '@nestjs/common'
import { GetTaskFormSchemaDto, TaskService } from '@flowda-projects/flowda-services'

@Controller('/tasks')
export class TaskController {
  constructor(private readonly taskService: TaskService) {}

  @Post('/getTaskForm')
  getTaskForm(@Body() dto: GetTaskFormSchemaDto) {
    return this.taskService.getTaskForm(dto)
  }

  @Get('/:taskId')
  getTask(@Param('taskId') taskId: string) {
    return this.taskService.getTask(taskId)
  }

  @Post('/:taskId/completeResource')
  completeResource(@Param('taskId') taskId: string, @Body() body: any) {
    return this.taskService.completeResource(taskId, body)
  }
}
