import { INestApplication } from '@nestjs/common'
import { TrpcRouter } from '@flowda-projects/flowda-services-trpc-server'
import { FLOWDA_ENV, UserService } from '@flowda-projects/flowda-services'
import { ExtractJwt } from 'passport-jwt'
import { createProxyMiddleware } from 'http-proxy-middleware'

export const globalPrefix = 'flowda-api'

export function setupNestApp(app: INestApplication) {
  app.enableCors()
  app.setGlobalPrefix(globalPrefix)
  const trpc = app.get(TrpcRouter)
  trpc.applyMiddleware(app, globalPrefix)

  const user = app.get(UserService)

  app.use((req, res, next) => {
    if (req.originalUrl.includes('favicon.ico')) {
      res.status(204).end()
    } else if (req.url.indexOf(`/${globalPrefix}/camunda/engine-rest/`) > -1) {
      const extract = ExtractJwt.fromAuthHeaderAsBearerToken()
      const bearerToken = extract(req)
      if (bearerToken) {
        try {
          const authed = user.verifyAccessToken(bearerToken)
          if (authed) {
            next()
          } else {
            res.status(401).json({
              message: '[PROXY] Unauthorized',
            })
          }
        } catch (e) {
          res.status(401).json({
            message: '[PROXY] Unauthorized',
          })
        }
      } else {
        res.status(401).json({
          message: '[PROXY] Unauthorized',
        })
      }
    } else {
      next()
    }
  })

  // Proxy endpoints
  app.use(
    `/${globalPrefix}/camunda/engine-rest`,
    createProxyMiddleware({
      target: FLOWDA_ENV.C7_REST_URL,
      changeOrigin: true,
      pathRewrite: {
        [`^/${globalPrefix}/camunda/engine-rest`]: '',
      },
    }),
  )
}
