import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-flowda'
import { PrismaClientSymbol, SdkProductCreateManyItemDto } from '@flowda-projects/flowda-shared-types'

@injectable()
export class ProductV4Service {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProductV4Service.name)
  }

  async createManyProducts(tid: number, list: SdkProductCreateManyItemDto[]): Promise<db.Product[]> {
    const data = list.map(item => ({
      ...item,
      tenantId: tid,
      productType: item.productType as db.ProductType,
      restricted: item.restricted || 0,
      amount: item.amount,
      //  prisma null
      plan: item.plan === undefined ? null : item.plan,
      extendedDescriptionData: item.extendedDescriptionData,
      fileSize: item.fileSize === undefined ? null : item.fileSize,
      storeDuration: item.storeDuration === undefined ? null : item.storeDuration,
      hasAds: item.hasAds === undefined ? null : item.hasAds,
      tecSupport: item.tecSupport === undefined ? null : item.tecSupport,
      validityPeriod: null,
    }))
    await this.prisma.product.createMany({ data })
    return this.prisma.product.findMany({
      where: {
        tenantId: tid,
      },
    })
  }
}
