import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-flowda'
import {
  DynamicTableSchemaTransformer,
  DynamicTableSchemaTransformerSymbol,
  PrismaClientSymbol,
} from '@flowda-projects/flowda-shared'

@injectable()
export class DynamicTableDefService {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(DynamicTableSchemaTransformerSymbol) private transformer: DynamicTableSchemaTransformer,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DynamicTableDefService.name)
  }

  async getRaw() {
    return this.prisma.dynamicTableDef.findMany({
      where: {
        isDeleted: false,
      },
      select: {
        name: true,
        extendedSchema: true,
        dynamicTableDefColumns: {
          where: {
            isDeleted: false,
          },
          select: {
            name: true,
            type: true,
            extendedSchema: true,
          },
        },
      },
    })
  }

  async getSchema() {
    const dbRet = await this.prisma.dynamicTableDef.findMany({
      where: {
        isDeleted: false,
      },
      select: {
        name: true,
        extendedSchema: true,
        dynamicTableDefColumns: {
          where: {
            isDeleted: false,
          },
          select: {
            name: true,
            type: true,
            extendedSchema: true,
          },
        },
      },
    })
    const schema = dbRet.reduce((acc, cur) => {
      const s = this.transformer.transform(cur)
      acc[s.schema_name] = s
      return acc
    }, {} as any)
    return schema
  }
}
