import { initTRPC, TRPCError } from '@trpc/server'
import { inject, injectable } from 'inversify'

import { LoggerService } from '@nestjs/common'
import { errorFormatter, transformer } from '@flowda-projects/flowda-shared-node'
import { PrismaClientSymbol, UserService } from '@flowda-projects/flowda-services'
import * as db from '@prisma/client-flowda'
import { Context } from './trpc.context'

// import { OpenApiMeta } from 'trpc-openapi'

@injectable()
export class TrpcService {
  private readonly logger

  constructor(
    @inject(UserService) private userService: UserService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(TrpcService.name)
  }

  trpc = initTRPC
    .context<Context>()
    // .meta<OpenApiMeta>()
    .create({
      transformer: transformer,
      errorFormatter: opts =>
        errorFormatter(opts, {
          log: async input => {
            try {
              await this.prisma.requestErrorLog.create({
                data: {
                  requestId: input.requestId,
                  tenantId: input.tenantId,
                  userId: input.userId,
                  log: input.log,
                },
              })
            } catch (e: any) {
              console.warn(`requestErrorLog.create error`, e.message)
            }
          },
        }),
    })

  procedure = this.trpc.procedure

  tenantProtectedProcedure = this.trpc.procedure.use(async opts => {
    const ctx = opts.ctx
    if (!ctx.tenant) {
      throw new TRPCError({ code: 'UNAUTHORIZED' })
    }
    return opts.next({
      ctx: {
        ...ctx,
        tenant: ctx.tenant,
      },
    })
  })

  userProtectedProcedure = this.trpc.procedure.use(async opts => {
    const ctx = opts.ctx
    if (!ctx.user) {
      throw new TRPCError({ code: 'UNAUTHORIZED' })
    }
    return opts.next({
      ctx: {
        ...ctx,
        user: ctx.user,
      },
    })
  })

  router = this.trpc.router
  mergeRouters = this.trpc.mergeRouters
}
