import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import type { DataService } from '@flowda-projects/flowda-shared'
import { DataServiceSymbol } from '@flowda-projects/flowda-shared'

@injectable()
export class DataRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(DataServiceSymbol) private dataService: DataService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DataRouter.name)
  }

  router = this.trpc.router({
    get: this.trpc.procedure
      .input(
        z.object({
          user: z.any(),
          path: z.string(),
          query: z.any(),
        }),
      )
      .query(async ({ input, ctx }) => {
        return this.dataService.get(input.user, input.path, input.query)
      }),

    put: this.trpc.procedure
      .input(
        z.object({
          user: z.any(),
          path: z.string(),
          values: z.any(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.dataService.put(input.user, input.path, input.values)
      }),
  })
}
