import { inject, injectable } from 'inversify'
import { ConflictException, ForbiddenException, InternalServerErrorException, type LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-flowda'
import {
  type createOrderUserJwtPayloadSchemaDto,
  type createQuickOrderTenantJWTPayloadSchemaDto,
  EPlan,
  PrismaClientSymbol,
  Serial_Max,
  Serial_Min,
  type transactionsNativeSchemaDto,
} from '@flowda-projects/flowda-shared-types'
import { WeixinPayService } from '../weixin-pay.service'
import { userSelect, UserService } from '../user.service'
import dayjs from 'dayjs'
import * as _ from 'radash'

export const orderSelect = db.Prisma.validator<db.Prisma.OrderSelect>()({
  id: true,
  serial: true,
  status: true,
  userId: true,
  tenantId: true,
  user: {
    select: _.omit(userSelect, ['hashedPassword', 'hashedRefreshToken']),
  },
  productSnapshots: {
    select: {
      id: true,
      snapshotPrice: true,
      productId: true,
    },
  },
})

@injectable()
export class OrderV4Service {
  private readonly logger: LoggerService

  constructor(
    @inject(WeixinPayService) private wxPayService: WeixinPayService,
    @inject(UserService) private user: UserService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(OrderV4Service.name)
  }

  async query(orderId: number) {
    const ret = await this.prisma.order.findMany({
      where: { id: orderId },
      select: orderSelect,
    })

    return ret
  }

  async queryPay(tid: number, userId: number, orderId: number) {
    this.logger.log(`querying order, uid:${userId}, orderId:${orderId}`)
    const userRet = await this.prisma.user.findUniqueOrThrow({
      where: {
        id: userId,
      },
    })
    const orderRet = await this.prisma.order.findFirstOrThrow({
      select: orderSelect,
      where: { id: orderId },
    })
    if (orderRet.userId !== userId) {
      throw new ForbiddenException('ID')
    }
    this.logger.log(`order found ${orderRet.id}, query request to wx`)
    const payQueryRet = await this.wxPayService.query(orderId)
    this.logger.log(`order query response from wx ${payQueryRet?.trade_state}`)
    if (payQueryRet.status !== 200 || payQueryRet.trade_state !== 'SUCCESS') {
      throw new InternalServerErrorException('')
    }
    this.logger.log(`order query success`)

    // todo:  productSnapshot  order 1-1
    const productId = orderRet.productSnapshots[0].productId
    const productRet = await this.prisma.product.findUniqueOrThrow({
      where: {
        id: productId,
      },
    })

    const [userRet2, payRet] = await this.prisma.$transaction([
      //  orderProfile
      this.prisma.user.update({
        where: {
          id: userId,
        },
        select: userSelect,
        data: {
          orderProfile: {
            upsert: {
              update: {
                productType: productRet.productType,
                plan: productRet.plan,
                amount: {
                  increment: productRet.amount || 0,
                },
              },
              create: {
                tenantId: userRet.tenantId,
                productType: productRet.productType,
                plan: productRet.plan,
                amount: productRet.amount,
                expireAt:
                  productRet.validityPeriod != null ? dayjs().add(productRet.validityPeriod, 'day').toDate() : null,
              },
            },
          },
        },
      }),
      // 
      this.prisma.pay.upsert({
        where: {
          orderId: orderId,
        },
        create: {
          tenantId: userRet.tenantId,
          userId: userRet.id,
          status: db.PayStatus.PAIED,
          orderId: orderId,
          transactionId: payQueryRet.transaction_id,
        },
        update: {
          status: db.PayStatus.PAIED,
          transactionId: payQueryRet.transaction_id,
        },
      }),
    ])

    return {
      order: orderRet,
      payQueryRet,
    }
  }

  async createNative(dto: createOrderUserJwtPayloadSchemaDto) {
    return this.createInner(dto, this.wxPayService.transactionsNative)
  }

  async createJSAPI(dto: createOrderUserJwtPayloadSchemaDto) {
    return this.createInner(dto, this.wxPayService.transactionsJSAPI)
  }

  /*
   callback  template method 
   */
  async createInner(
    dto: createOrderUserJwtPayloadSchemaDto,
    transactionCallback: (input: transactionsNativeSchemaDto) => any,
  ) {
    this.logger.log(`creating order:tid:${dto.tid},uid:${dto.uid},productId:${dto.productId}`)

    const productRet = await this.prisma.product.findUniqueOrThrow({
      where: {
        id: dto.productId,
      },
    })

    if (productRet.restricted /**/) {
      const purchased = await this.prisma.order.count({
        where: {
          tenantId: dto.tid,
          userId: dto.uid,
          productSnapshots: {
            some: {
              productId: dto.productId,
            },
          },
        },
      })
      if (purchased > 0) {
        throw new ForbiddenException(`Product is restricted`, {
          description: `order history length: ${purchased}`,
        })
      }
    }

    const lastRet = await this.prisma.order.findFirst({
      orderBy: [
        {
          createdAt: 'desc',
        },
      ],
    })
    let serial
    if (lastRet == null || lastRet.serial >= Serial_Max) {
      serial = Serial_Min
    } else {
      serial = lastRet.serial + 1
    }

    if (productRet.price.toNumber() === 0 /**/) {
      const orderProfileRet = await this.prisma.orderProfile.findUnique({
        where: {
          userId: dto.uid,
        },
      })
      if (orderProfileRet && orderProfileRet.plan === EPlan.VIP) {
        throw new ConflictException(`User has a paid plan`, {
          description: `profileId:${orderProfileRet.id}`,
        })
      }

      const [orderRet, userRet] = await this.prisma.$transaction([
        this.prisma.order.create({
          data: {
            tenantId: dto.tid,
            userId: dto.uid,
            status: db.OrderStatus.FREE_DEAL,
            serial: serial,
            productSnapshots: {
              create: {
                snapshotPrice: productRet.price,
                productId: productRet.id,
                userId: dto.uid,
                tenantId: dto.tid,
              },
            },
          },
          select: orderSelect,
        }),
        this.prisma.user.update({
          where: {
            id: dto.uid,
          },
          select: userSelect,
          data: {
            orderProfile: {
              upsert: {
                update: {
                  productType: productRet.productType,
                  plan: productRet.plan,
                  amount: {
                    increment: productRet.amount || 0,
                  },
                },
                create: {
                  tenantId: dto.tid,
                  productType: productRet.productType,
                  plan: productRet.plan,
                  amount: productRet.amount,
                  expireAt:
                    productRet.validityPeriod != null ? dayjs().add(productRet.validityPeriod, 'day').toDate() : null,
                },
              },
            },
          },
        }),
      ])
      this.logger.log(`order created: ${orderRet.id}`)
      return {
        order: orderRet,
        wxRet: null,
      }
    }

    // 
    // 1. 
    const orderRet = await this.prisma.order.create({
      data: {
        tenantId: dto.tid,
        userId: dto.uid,
        status: db.OrderStatus.INITIALIZED,
        serial: serial,
        productSnapshots: {
          create: {
            snapshotPrice: productRet.price,
            productId: productRet.id,
            tenantId: dto.tid,
            userId: dto.uid,
          },
        },
      },
      select: orderSelect,
    })

    try {
      // 2.  
      // todo 
      const wxRet = await transactionCallback({
        openid: dto.openid,
        orderId: String(orderRet.id),
        desc: productRet.name,
        total: productRet.price.toNumber(),
      })
      const orderRet2 = await this.prisma.order.update({
        where: {
          id: orderRet.id,
        },
        data: {
          status: db.OrderStatus.PAY_ASSOCIATED,
        },
        select: orderSelect,
      })
      return {
        order: orderRet2,
        wxRet: wxRet,
      }
    } catch (e) {
      this.logger.error(e)
      return {
        order: orderRet,
        wxRet: {
          success: false,
        },
      }
    }
  }

  async createQuick(dto: createQuickOrderTenantJWTPayloadSchemaDto) {
    const userRet = await this.user.createQuick(dto)
    return this.createNative({
      tid: userRet.tenantId,
      uid: userRet.id,
      productId: dto.productId,
    })
  }

  async queryPayQuick(tid: number, anonymousCustomerToken: string, orderId: number) {
    const userRet = await await this.prisma.user.findFirstOrThrow({
      where: {
        username: anonymousCustomerToken,
        tenantId: tid,
      },
    })
    this.logger.debug!(`start queryPay`)
    const ret = await this.queryPay(tid, userRet.id, orderId)
    this.logger.debug!(`succeed queryPay`)
    const openid = ret.payQueryRet.payer.openid

    const updateUserRet = await this.prisma.user.update({
      select: userSelect,
      where: {
        id: userRet.id,
      },
      data: {
        username: openid,
        weixinProfile: {
          connectOrCreate: {
            where: {
              unionOrOpenid: openid,
            },
            create: {
              unionOrOpenid: openid,
              loginOpenid: openid,
            },
          },
        },
      },
    })
    return {
      ...ret,
      ...{
        user: updateUserRet,
      },
    }
  }
}
