import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import { ConflictException, type LoggerService } from '@nestjs/common'
import { z } from 'zod'
import {
  _resetTenantPasswordSchema,
  accountExistsSchema,
  amountUpdateSchema,
  appCreateV4Schema,
  createQuickOrderSchema,
  customerPreSignupSchema,
  customerSignupSchema,
  EPlan,
  findByUnionIdAndTenantIdSchema,
  fwhLoginSchema,
  generateRecoveryCodeSchema,
  getTenantByNameSchema,
  PrismaClientSymbol,
  refreshTokenSchema,
  registerByUnionIdSchema,
  resetPasswordWithRecoveryCodeSchema,
  sendSmsVerifyCodeSchema,
  tenantJwtPayloadSchema,
  updateFreeProfileSchema,
  updatePaidProfileSchema,
  userJwtPayloadSchema,
  validateByEmailSchema,
  validateSchema,
  validateTenantSchema,
  verifyMobileSchema,
  wxValidateUserSchema,
} from '@flowda-projects/flowda-shared-types'
import * as db from '@prisma/client-flowda'
import * as _ from 'radash'
import { userSelect, UserService } from '@flowda-projects/flowda-services'
import dayjs from 'dayjs'

@injectable()
export class UserRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(UserService) private userService: UserService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserRouter.name)
  }

  userRouter = this.trpc.router({
    getTenant: this.trpc.procedure.input(z.object({ tid: z.number() })).query(async ({ input }) => {
      return this.userService.getTenantInfo(input.tid)
    }),

    sendSmsVerifyCode: this.trpc.procedure.input(sendSmsVerifyCodeSchema).mutation(async opts => {
      return this.userService.sendSmsVerifyCode(opts.input, opts.ctx)
    }),

    verifyMobile: this.trpc.procedure.input(verifyMobileSchema).mutation(async ({ input, ctx }) => {
      return this.userService.verifyMobile(input, ctx)
    }),

    accountExists: this.trpc.procedure.input(accountExistsSchema).query(async ({ input }) => {
      return this.userService.accountExists(input)
    }),

    findByUnionIdAndTenantId: this.trpc.procedure.input(findByUnionIdAndTenantIdSchema).query(async ({ input }) => {
      return this.userService.findByUnionIdAndTenantId(input)
    }),

    findByNameAndTenantId: this.trpc.procedure
      .input(
        z.object({
          username: z.string(),
          tid: z.number(),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.prisma.user.findFirst({
          where: {
            username: input.username,
            tenantId: input.tid,
          },
        })
        return ret
      }),

    findOrThrowByNameAndTenantId: this.trpc.procedure
      .input(
        z.object({
          username: z.string(),
          tid: z.number(),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.prisma.user.findFirstOrThrow({
          where: {
            username: input.username,
            tenantId: input.tid,
          },
        })
        return ret
      }),

    registerByUnionId: this.trpc.procedure.input(registerByUnionIdSchema).mutation(opts => {
      return this.userService.registerByUnionId(opts.input, opts.ctx)
    }),

    getTenantByName: this.trpc.procedure.input(getTenantByNameSchema).query(async ({ input }) => {
      return this.userService.getTenantByName(input)
    }),

    validateTenant: this.trpc.procedure.input(validateTenantSchema).mutation(async opts => {
      return this.userService.validateTenant(opts.input, opts.ctx)
    }),

    refreshTenantToken: this.trpc.procedure
      .input(
        z.object({
          rt: z.string(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.userService.refreshTenantToken(input.rt, ctx)
      }),

    validate: this.trpc.procedure.input(validateSchema).mutation(async ({ input }) => {
      return this.userService.validate(input)
    }),

    validateByEmail: this.trpc.procedure.input(validateByEmailSchema).query(async ({ input, ctx }) => {
      return this.userService.validateByEmail(input, ctx)
    }),

    findMany: this.trpc.procedure
      .input(
        z.object({
          userIds: z.array(z.number()),
        }),
      )
      .query(async ({ input }) => {
        return this.prisma.user.findMany({
          where: {
            id: {
              in: input.userIds,
            },
          },
          select: userSelect,
        })
      }),

    updateUserInfo: this.trpc.procedure
      .input(
        z.object({
          userId: z.number(),
          email: z.string().optional(),
          username: z.string().optional(),
          image: z.string().optional(),
        }),
      )
      .mutation(async ({ input }) => {
        const { username, email, image } = input
        return this.prisma.user.update({
          where: {
            id: input.userId,
          },
          data: {
            ...(username && { username }),
            ...(email && { email }),
            ...(image && { image }),
          },
        })
      }),

    findUnique: this.trpc.procedure
      .input(
        z.object({
          email: z.string().optional(),
          id: z.number().optional(),
        }),
      )
      .query(async ({ input }) => {
        const userRet = await this.prisma.user.findUniqueOrThrow({
          where: input,
          select: userSelect,
        })
        return _.omit(userRet, ['hashedRefreshToken', 'hashedPassword'])
      }),

    verifyAccessToken: this.trpc.procedure
      .input(
        z.object({
          jwt: z.string(),
        }),
      )
      .query(async ({ input }) => {
        return this.userService.verifyAccessToken(input.jwt)
      }),

    createTenant: this.trpc.procedure.input(appCreateV4Schema).mutation(async ({ input, ctx }) => {
      return this.userService.createTenant(input, ctx)
    }),

    preSignup: this.trpc.procedure
      .input(customerPreSignupSchema.merge(tenantJwtPayloadSchema))
      .mutation(async ({ input, ctx }) => {
        return this.userService.preSignup(input, ctx)
      }),

    verifyAndSignup: this.trpc.procedure
      .input(customerSignupSchema.merge(tenantJwtPayloadSchema))
      .mutation(async ({ input, ctx }) => {
        return this.userService.verifyAndSignup(input, ctx)
      }),

    wxValidateUser: this.trpc.procedure
      .input(wxValidateUserSchema.merge(tenantJwtPayloadSchema))
      .mutation(async ({ input, ctx }) => {
        return this.userService.wxValidateUser(input, ctx)
      }),

    refreshToken: this.trpc.procedure.input(refreshTokenSchema).mutation(async ({ input, ctx }) => {
      return this.userService.refreshToken(input, ctx)
    }),

    logout: this.trpc.procedure.input(userJwtPayloadSchema).mutation(async ({ input }) => {
      return this.prisma.user.update({
        where: {
          id: input.uid,
        },
        data: {
          hashedRefreshToken: null,
        },
      })
    }),

    generateRecoveryCode: this.trpc.procedure
      .input(generateRecoveryCodeSchema.merge(tenantJwtPayloadSchema))
      .mutation(async ({ input, ctx }) => {
        return this.userService.generateRecoveryCode(input, ctx)
      }),

    resetPasswordWithRecoveryCode: this.trpc.procedure
      .input(resetPasswordWithRecoveryCodeSchema.merge(tenantJwtPayloadSchema))
      .mutation(async ({ input, ctx }) => {
        return this.userService.resetPasswordWithRecoveryCode(input, ctx)
      }),

    updatePaidProfile: this.trpc.procedure
      .input(updatePaidProfileSchema.merge(userJwtPayloadSchema))
      .mutation(async ({ input }) => {
        return this.prisma.user.update({
          where: {
            id: input.uid,
          },
          select: userSelect,
          data: {
            orderProfile: {
              upsert: {
                update: {
                  productType: input.product.productType,
                  plan: input.product.plan,
                  amount: {
                    increment: input.product.amount || 0,
                  },
                },
                create: {
                  tenantId: input.tid,
                  productType: input.product.productType,
                  plan: input.product.plan,
                  amount: input.product.amount,
                  expireAt:
                    input.product.validityPeriod != null
                      ? dayjs().add(input.product.validityPeriod, 'day').toDate()
                      : null,
                },
              },
            },
          },
        })
      }),

    updateFreeProfile: this.trpc.procedure
      .input(updateFreeProfileSchema.merge(userJwtPayloadSchema))
      .mutation(async ({ input }) => {
        // 
        const orderProfileRet = await this.prisma.orderProfile.findUnique({
          where: {
            userId: input.uid,
          },
        })
        if (orderProfileRet && orderProfileRet.plan === EPlan.VIP) {
          throw new ConflictException(`User has a paid plan`, {
            description: `profileId:${orderProfileRet.id}`,
          })
        }

        return this.prisma.user.update({
          where: {
            id: input.uid,
          },
          select: userSelect,
          data: {
            orderProfile: {
              upsert: {
                update: {
                  productType: input.product.productType,
                  plan: input.product.plan,
                  amount: {
                    increment: input.product.amount || 0,
                  },
                },
                create: {
                  tenantId: input.tid,
                  productType: input.product.productType,
                  plan: input.product.plan,
                  amount: input.product.amount,
                  expireAt:
                    input.product.validityPeriod != null
                      ? dayjs().add(input.product.validityPeriod, 'day').toDate()
                      : null,
                },
              },
            },
          },
        })
      }),

    fwhLogin: this.trpc.procedure.input(fwhLoginSchema.merge(tenantJwtPayloadSchema)).mutation(async ({ input }) => {
      return this.userService.fwhLogin(input)
    }),

    amountUpdate: this.trpc.procedure
      .input(amountUpdateSchema.merge(userJwtPayloadSchema))
      .mutation(async ({ input }) => {
        return this.userService.amountUpdate(input)
      }),

    createQuick: this.trpc.procedure
      .input(createQuickOrderSchema.merge(tenantJwtPayloadSchema))
      .mutation(async ({ input }) => {
        return this.userService.createQuick(input)
      }),

    updateAnonymousToPayOpenId: this.trpc.procedure
      .input(
        z.object({
          tid: z.number(),
          id: z.number(),
          openid: z.string(),
        }),
      )
      .mutation(async ({ input }) => {
        return this.prisma.user.update({
          select: userSelect,
          where: {
            id: input.id,
          },
          data: {
            username: input.openid,
            weixinProfile: {
              connectOrCreate: {
                where: {
                  loginOpenid: input.openid,
                },
                create: {
                  unionOrOpenid: input.openid,
                  loginOpenid: input.openid,
                },
              },
            },
          },
        })
      }),
    _resetTenantPassword: this.trpc.procedure.input(_resetTenantPasswordSchema).mutation(async ({ input }) => {
      return this.userService._resetTenantPassword(input)
    }),
  })
}
