import { inject, injectable } from 'inversify'
import { TrpcService } from '../trpc.service'
import { type LoggerService } from '@nestjs/common'
import {
  amountUpdateSchema,
  customerPreSignupSchema,
  customerSignupSchema,
  generateRecoveryCodeSchema,
  resetPasswordWithRecoveryCodeSchema,
} from '@flowda-projects/flowda-shared-types'
import { UserService } from '@flowda-projects/flowda-services'
import { z } from 'zod'

@injectable()
export class CustomerAuthV4Router {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(UserService) private userService: UserService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(CustomerAuthV4Router.name)
  }

  router = this.trpc.router({
    preSignup: this.trpc.tenantProtectedProcedure.input(customerPreSignupSchema).mutation(async ({ input, ctx }) => {
      return this.userService.preSignup(Object.assign({ tid: ctx.tenant.id }, input))
    }),
    verifyAndSignup: this.trpc.tenantProtectedProcedure.input(customerSignupSchema).mutation(async ({ input, ctx }) => {
      return this.userService.verifyAndSignup(Object.assign({ tid: ctx.tenant.id }, input), ctx)
    }),
    validateUserReturnTokens: this.trpc.tenantProtectedProcedure
      .input(
        z.object({
          email: z.string(),
          password: z.string(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.userService.validateByEmail({
          tenantId: ctx.tenant.id,
          email: input.email,
          password: input.password,
        })
      }),
    wxValidateUser: this.trpc.tenantProtectedProcedure
      .input(
        z.object({
          code: z.string(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.userService.wxValidateUser(
          {
            tid: ctx.tenant.id,
            code: input.code,
          },
          ctx,
        )
      }),
    refreshToken: this.trpc.tenantProtectedProcedure
      .input(
        z.object({
          rt: z.string(),
        }),
      )
      .mutation(async ({ input }) => {
        return this.userService.refreshToken(input)
      }),
    getUser: this.trpc.userProtectedProcedure.input(z.object({})).query(async ({ input, ctx }) => {
      return ctx.user
    }),
    logoutApi: this.trpc.userProtectedProcedure.input(z.object({})).mutation(async ({ input, ctx }) => {
      return this.userService.logout({
        uid: ctx.user.id,
        tid: ctx.user.tenantId,
      })
    }),
    generateRecoveryCode: this.trpc.tenantProtectedProcedure
      .input(generateRecoveryCodeSchema)
      .mutation(async ({ input, ctx }) => {
        return this.userService.generateRecoveryCode({
          ...{
            tid: ctx.tenant.id,
          },
          ...input,
        })
      }),
    resetPassword: this.trpc.tenantProtectedProcedure
      .input(resetPasswordWithRecoveryCodeSchema)
      .mutation(async ({ input, ctx }) => {
        return this.userService.resetPasswordWithRecoveryCode({
          ...{
            tid: ctx.tenant.id,
          },
          ...input,
        })
      }),
    fwhLogin: this.trpc.tenantProtectedProcedure
      .input(
        z.object({
          code: z.string(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        return this.userService.fwhLogin({
          tid: ctx.tenant.id,
          code: input.code,
        })
      }),
    amountUpdate: this.trpc.userProtectedProcedure.input(amountUpdateSchema).mutation(async ({ input, ctx }) => {
      return this.userService.amountUpdate({
        ...{
          uid: ctx.user.id,
          tid: ctx.user.tenantId,
        },
        ...input,
      })
    }),
  })
}
